import React from "react";
import "../App.css";

function Card(props) {
    return (
        <div className="flags-subcontainer">
            <h3>{props.country}</h3>
            <img
                alt={props.name}
                src={props.img}
                onClick={() => props.clickedCard(props.id)}
                className="images"
            />
        </div>
    );
}
              
export default Card;
